@import "./shared.scss";

* {
	box-sizing: border-box;
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
		"Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
		"Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.nav {
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	background: transparent;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 1.5rem 6vw;
	z-index: 2;

	ul {
		list-style: none;
	}
	li {
		cursor: pointer;
		display: inline-block;
		padding: 0 1vw;
		font-size: 0.8rem;
		font-weight: 500;
	}
}

.nav-left {
	display: flex;
}

.nav-right {
	display: flex;
	align-items: center;
	color: white;
	font-size: 0.9rem;
	div {
		cursor: pointer;
		margin: 0 1rem;
	}
}

.nav-brand {
	display: flex;
	align-items: center;
	cursor: pointer;

	div {
		cursor: pointer;
		font-weight: 500;
		text-transform: uppercase;
		padding: 0 1rem;
		letter-spacing: 0.2rem;
	}
}

.nav-mobile-bar {
	display: none;
}

.landing {
	width: 100%;
	display: flex;
	justify-content: center;
}

.landing-left {
	height: 100vh;
	position: relative;
	width: 50vw;
	.container {
		padding: 7rem 2vw 0 8vw;
	}
	&:after {
		content: "";
		width: 16vw;
		height: 16vw;
		background: $light-yellow;
		border-bottom-left-radius: 100%;
		position: absolute;
		top: 0;
		right: 0;
	}
}

h1 {
	position: relative;
	z-index: 2;
	font-weight: 300;
	// font-size: 3.8rem;
	// font-size: calc(100% + 3rem);
	// font-size: calc(2.5rem + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
	font-size: 4vw;
	text-decoration: underline;
	line-height: 6vw;
	text-underline-offset: 1vw;
	text-decoration-thickness: 3.5px;
}

.landing-second {
	color: $sec-text;
	font-size: 1.2vw;
	max-width: 25rem;
}

.landing-email {
	background: #f7f9fb;
	margin-top: 3rem;
	margin-right: 1rem;
	padding: 1rem;
	color: $sec-text;
	border-radius: 2rem;
	display: inline-block;
	min-width: 22rem;
	border: none;

	&:focus {
		outline: none;
	}
}

.landing-right {
	position: relative;
	height: 100vh;
	background: $light-blue;
	width: 100%;
	padding: 5rem 3vw;
	width: 50vw;
}

.landing-slider {
	max-height: 90%;
	display: flex;
	padding-top: 2rem;
	justify-content: flex-end;
}

.landing-slider-image {
	position: absolute;
	max-width: 70%;
	bottom: 14rem;
}

.slider-tags {
	position: absolute;
	display: flex;
	flex-direction: column;
}

.slider-tag-bar {
	position: relative;
	z-index: 2;
	background: white;
	min-width: 15vw;
	height: 3rem;
	border-radius: 20px;
	display: flex;
	align-items: center;
	margin: 0.4rem 0;

	.bar-text {
		color: $sec-text;
		font-size: 0.8rem;
		font-weight: 500;
		margin-right: 2.8rem;
	}

	.stars {
		transform: translateY(-2.5px);
		margin-right: 0.5rem;
	}
}

.bar {
	margin: 0 0.5rem;
	padding: 0.4rem 0.6rem;
	border-radius: 50%;
}

.bar1 {
	color: $dark-green;
	background: $light-green;
}

.bar2 {
	padding: 0.55rem;
	font-size: 0.75rem;
	color: $dark-pink;
	background: $light-pink;
}

.bar3 {
	padding: 0.55rem;
	font-size: 0.75rem;
	color: $dark-yellow;
	background: rgba($color: $light-yellow, $alpha: 0.5);
}

.slider-controller {
	position: absolute;
	display: flex;
	bottom: 0;
	left: 0;
	background: $dark-blue;
	width: 100%;
	height: 14rem;
	padding: 3vw;
	justify-content: flex-start;
	align-items: center;

	&::after {
		content: "";
		width: 30%;
		height: 14rem;
		background: #f9b8c4;
		border: 100%;
		position: absolute;
		top: 0;
		right: 0;
	}

	ul {
		list-style: none;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
	}
	li {
		font-size: 0.5rem;
		color: white;
		padding: 0.5rem;
	}

	.slider-desc {
		color: white;
		font-weight: 600;
		font-size: 0.9rem;
		line-height: 0.5rem;
		padding: 0 2.5rem;
		p {
			font-weight: 400;
			font-size: 0.8rem;
		}
	}
}

.arrow {
	position: absolute;
	transform-origin: center;
	z-index: 2;
	font-size: 1.2vw;
	right: 26.5%;
	background: white;
	border-radius: 50%;
	padding: 1rem 1.2rem;
	cursor: pointer;
}

.slider-arrow-up {
	bottom: calc(4.2rem + 4vw);
}

.slider-arrow-down {
	top: calc(4.2rem + 4vw);
}

.landing-btn {
	display: inline-block;
	border-radius: 2rem;
	padding: 0.8rem 1.2rem;
	color: black;
	font-size: 0.8rem;
	font-weight: 600;
	cursor: pointer;
	text-align: center;
	text-transform: none;
}

@media (max-width: 60rem) {
	.brand-text {
		display: none;
	}
	.arrow {
		right: 25%;
	}
	.slider-arrow-up {
		bottom: calc(3rem + 10vw);
	}

	.slider-arrow-down {
		top: calc(3rem + 10vw);
	}
}

@media (max-width: 40rem) {
	.nav {
		padding: 1.5rem 2.5rem;
	}
	.nav-left ul {
		display: none;
	}
	.nav-right {
		display: none;
	}
	.nav-brand {
		align-items: flex-start;
	}
	.nav-mobile-bar {
		display: inline-block;
	}
	.landing {
		flex-wrap: wrap;
	}

	h1 {
		font-size: 7vh;
		line-height: 10vh;
		text-underline-offset: 3vw;
		br {
			display: none;
		}
	}

	.landing-left {
		width: 100%;
		.container {
			padding: 8rem 0vw 0 5vw;
		}
		&::after {
			content: none;
		}
	}

	.landing-email {
		min-width: 18rem;
	}

	.landing-right {
		width: 100%;
	}

	.landing-btn-xl {
		display: block;
		max-width: 18rem;
		border-radius: 5rem;
		background: black;
		padding: 1.5rem 4.5rem;
		color: white;
		text-transform: uppercase;
		margin-top: 1rem;
	}
	.landing-second {
		font-size: 0.8rem;
		max-width: 80%;
	}
	.arrow {
		font-size: 0.8rem;
		right: 24%;
	}
	.slider-arrow-up {
		bottom: calc(7.5rem);
	}

	.slider-arrow-down {
		top: calc(7.5rem);
	}
}

.features {
	display: grid;
	grid-template-columns: repeat(4, 25%);
	grid-template-rows: auto auto;
}

.features-head {
	grid-column: span 2;
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding: 8% 15%;

	.landing-second {
		padding: 0;
	}
}

.features-card {
	position: relative;
	background: rgba($color: $sec-text, $alpha: 0.05);
	width: 100%;
	height: 25vw;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding: 10%;
	gap: 0.5%;

	.title {
		padding: 0 1vw;
		max-width: 60%;
		// font-size: (1.6 * 1vw);
		font-size: 1.6vw;
	}
	.arrow {
		transform: scale(0.8);
		bottom: 10%;
		right: 15%;
	}
	&:nth-child(4) {
		grid-column-start: 2;
	}
	&:nth-child(odd) {
		background: white;
	}
}

h2 {
	font-weight: 300;
	font-size: 2.5vw;
	text-decoration: underline;
	line-height: 4vw;
	text-underline-offset: 0.7vw;
	text-decoration-thickness: 3px;
}

@media (max-width: 40rem) {
	.features {
		grid-template-columns: repeat(5, 80vw);
		grid-auto-flow: column;
		overflow-x: scroll;
		column-gap: 1.5rem;
		cursor: grab;
		&::-webkit-scrollbar {
			width: 0px; /* Remove scrollbar space */
			background: transparent; /* Optional: just make scrollbar invisible */
			display: none;
		}
	}

	// &::-webkit-scrollbar {
	// 	display: none;
	// }

	.features-head {
		grid-column: span 1;
		align-items: center;
		width: 100vw;
		padding: 8% 10%;
		position: sticky;
		left: 0;
	}
	h2 {
		font-size: 4vh;
		line-height: 6vh;
		text-underline-offset: 2vw;
		text-decoration-thickness: 2px;
		br {
			display: none;
		}
	}

	.landing-second {
		align-self: flex-start;
	}
	.features-card {
		margin: 1rem;
		height: 60vw;
		max-width: 80vw;
		padding: 10% 10%;
		gap: 1rem;
		grid-row-start: 2;
		background: rgba($color: $sec-text, $alpha: 0.05) !important;

		.card-image {
			transform: scale(3);
		}
		.title {
			font-size: 1.5rem;
			max-width: 70%;
			padding: 0;
		}
		&:nth-child(4) {
			grid-column-start: 3;
		}
	}
}

.profiles {
	height: 100vh;
	background: $dark-blue;
	overflow: hidden;
	position: relative;
	&::before {
		content: "";
		height: 100px;
		width: 20px;
		border-right: 25vw solid #f9fafb;
		border-bottom: 100vh solid #f9fafb;
	}
	&:after {
		content: "";
		width: 50%;
		height: 50vw;
		background: $light-blue;
		border-right: 100vw solid $light-blue;
		border-bottom: 100vw solid $light-blue;
		border-radius: 50%;
		position: absolute;
		top: -200vh;
		right: -100vw;
	}
}

.profiles-head {
	position: relative;
	display: flex;
	padding: 2rem 5rem;
	gap: 11vw;
	align-items: center;
	width: 100%;
	z-index: 2;

	.count {
		font-size: 1.2vw;
		font-weight: 500;
	}

	.profiles-controller {
		font-size: 1vw;
		background: white;
		border-radius: 50%;
		padding: 1rem 1.1rem;
		cursor: pointer;
		margin: 0 0.5rem;
	}
	.profiles-btn {
		margin-left: auto;
	}
}

.profiles-cards {
	display: grid;
	grid-template-columns: repeat(5, auto);
	grid-auto-flow: column;
	overflow: hidden;
	overflow-x: scroll;
	cursor: grab;
	user-select: none;
	gap: 30px;
	&::-webkit-scrollbar {
		width: 0px; /* Remove scrollbar space */
		background: transparent; /* Optional: just make scrollbar invisible */
	}
}

.profiles-card {
	position: relative;
	z-index: 2;
	background: white;
	border-radius: 15px;
	width: 550px;
	height: 350px;
	border-top: 25px solid black;
	display: flex;
	box-shadow: 4px 4px 10px rgba($color: black, $alpha: 0.3);
	padding: 2rem 3rem;
	margin: 10%;
	gap: 3rem;

	&:first-child {
		margin-left: 10%;
	}

	.pc-left {
		display: flex;
		flex-direction: column;
		gap: 1rem;
	}

	.pc-avatar {
		height: 100px;
		width: 100px;
		border-radius: 50%;
		background: $dark-pink;
		box-shadow: 0px 15px 20px rgba($color: black, $alpha: 0.5);
	}

	.pc-audio {
		height: 25px;
		width: 100px;
		background: $light-yellow;
		border-radius: 10px;
	}

	.pc-desc {
		display: flex;
		flex-direction: column;
		font-size: 0.8rem;
		padding: 0.5rem 0;
		gap: 0.6rem;
		width: 100%;

		.pc-name {
			font-weight: 500;
			font-size: 0.9rem;
		}
		.pc-country {
			display: flex;
			gap: 0.5rem;
			align-items: center;
		}
		.pc-flag {
			width: 20px;
		}
		.pc-flag_name {
			color: $sec-text;
			font-weight: 500;
		}
		.pc-title {
			font-weight: 400;
		}
		.pc-price {
			font-size: 0.7rem;
			font-weight: 500;
			color: $light-blue;
		}
		.pc-label {
			display: inline-block;
			color: $sec-text;
			min-width: 6rem;
			margin-bottom: 0.5rem;
		}
		.pc-btn {
			margin-top: 1rem;
			margin-left: auto;
		}
		.pc-interview {
			padding-top: 1.5rem;
			margin-top: 1rem;
			border-top: 0.1rem solid rgba($color: $sec-text, $alpha: 0.5);
		}
	}
}

@media (max-width: 40rem) {
	.profiles {
		&::before {
			content: none;
		}
	}
	.profiles-cards {
		gap: 1rem;
		margin-top: 2rem;
	}
	.profiles-card {
		width: 80vw;
		height: 400px;
		gap: 1rem;
	}
	.pc-interview {
		position: absolute;
		width: 90%;
		bottom: 50px;
		left: 5%;
	}
	.profiles-head {
		padding: 3rem 2rem;
		.count {
			font-size: 1rem;
			font-weight: 500;
		}
		.pc-controllers {
			margin-left: auto;
		}
		.profiles-controller {
			font-size: 0.8rem;
			background: white;
			border-radius: 50%;
			padding: 1rem 1.1rem;
			cursor: pointer;
			margin: 0 0.5rem;
		}
		.profiles-btn {
			display: none;
		}
	}
}

.cta-section {
	background: rgba($color: $sec-text, $alpha: 0.1);
	padding: 4rem 10vw;
	width: 100%;
	display: flex;
	align-items: center;
	gap: 2vw;

	.landing-email {
		margin-top: 0;
		min-width: 25vw;
		max-width: 30vw;
	}

	h2 {
		flex-basis: 30vw;
	}
}

.footer {
	position: relative;
	width: 100%;
	background-color: #1a49ba;
	color: #fff;
	overflow: hidden;

	a {
		color: #fff;
		text-decoration: none;
	}
}

.footer-bg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 0;
	display: flex;
	flex-direction: row;
	overflow: hidden;
}

.footer-bg-part {
	position: relative;
	width: 50%;
	height: 100%;
	min-height: 50vw;
	overflow: hidden;
}

.footer-bg-left {
	right: 0;
	transform: translate(50%, -50%);
	background-color: #ffc800;
	width: calc((728px) * 2);
	height: calc((728px) * 2);
}

.footer-bg-right {
	right: 0;
	transform: translate(0%, -50%);
	background-color: $light-blue;
	width: calc((728px) * 2);
	height: calc((728px) * 2);
}

.footer-bg-circle {
	display: block;
	width: calc(100 * (1vw));
	height: calc(100 * (1vw));
	border-radius: 50%;
	position: absolute;
	top: 0;
}

.footer-inner {
	width: 100%;
	max-width: 1920px;
	margin: 0 auto;
	position: relative;
}

.footer-menu {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	padding: 4.3rem 0 4.25rem 32.36111vw;
	padding: 4.3rem 0 4.25rem calc(32.36111 * (1vw));
	position: relative;
	z-index: 1;
	font-size: 0.9375rem;
	line-height: 1.27;

	& > ul {
		display: grid;
		grid-template-columns: 26.73611vw 16.875vw 24.02778vw;
		grid-template-columns:
			calc(26.73611 * (1vw)) calc(16.875 * (1vw))
			calc(24.02778 * (1vw));
		grid-template-rows: 1fr 1fr;
		grid-auto-flow: column;
		grid-gap: 3rem 0;
		gap: 3rem 0;

		& > li:first-child,
		& > li:nth-child(2) {
			padding: 0 13.19444vw 0 0;
		}
		& > li {
			padding: 0 1.38889vw 0 0;
		}
	}

	ul {
		list-style: none;
		padding: 0;
	}

	h3 {
		font-weight: 400;
		font-size: 1.3rem;
		line-height: 1;
		margin: 0 0 1rem;
	}

	.footer-sub-item {
		margin: 0 0 0.5rem;
	}

	a {
		font-style: normal;
		font-weight: 500;
		font-size: 0.85rem;
		line-height: 1.27;
	}
}

@media (max-width: 60rem) {
	.footer-bg-left {
		width: calc((683px) * 2);
		height: calc((683px) * 2);
	}
	.footer-bg-right {
		width: calc((683px) * 2);
		height: calc((683px) * 2);
	}
	.footer-menu {
		font-size: 0.65rem;
		h3 {
			font-size: 1rem;
		}
		a {
			font-size: 0.7125rem;
		}
	}
}
